@import '../../fonts/fonts.css';

.container {
  height: 100vh;
  box-sizing: border-box;
  margin-bottom: 10rem;
}
.custom-button-container {
  display: flex;
  justify-content: space-between;
}

.custom-button {
  flex: 1;
  border: none;
  border-radius: 0;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.custom-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: -1;
}

.custom-accordion .chakra-accordion__item {
  border-top: none !important; /* Remove the top border */
}

